/* Reset básico */
* {
  box-sizing: border-box;
}

/* Tipografía y Fondo */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #ffeefc; /* Fondo rosado suave */
  margin: 20px;
  padding: 0;
  color: #333;
  display: flex;
  justify-content: center; /* Centra horizontalmente */
  align-items: center; /* Centra verticalmente */
  height: 100vh; /* Altura completa de la ventana */
}

/* Títulos */
h1, h2, h3, h4, h5, h6 {
  color: #ff66b2; /* Tono de rosa */
  font-weight: bold;
  margin-top: 0;
}

/* Elementos de código */
code {
  font-size: 1.2em;
  background-color: #fff0f5;
  border-radius: 5px;
  padding: 2px 4px;
}

/* Listas */
ul {
  padding-inline-start: 20px;
}

/* Contenedor del juego */
.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

/* Estilo de los cuadrados del tablero */
.square {
  background: #ffc0cb; /* Fondo rosado */
  border: 2px solid #ff66b2; /* Borde rosa más oscuro */
  float: left;
  font-size: 48px; /* Tamaño del texto dentro de los cuadrados */
  font-weight: bold;
  line-height: 80px; /* Altura de la línea para centrar el texto verticalmente */
  height: 80px; /* Altura de cada cuadrado */
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 80px; /* Ancho de cada cuadrado */
  color: #fff; /* Texto blanco */
  transition: background-color 0.3s ease; /* Transición suave */
}

/* Fila del tablero */
.board-row:after {
  clear: both;
  content: '';
  display: table;
}

/* Estado del juego */
.status {
  margin-bottom: 20px;
  font-size: 1.5em;
  color: #ff66b2;
  text-align: center;
}

/* Información del juego */
.game-info {
  margin-left: 20px;
  color: #ff66b2;
}

/* Botones de movimiento */
button {
  background-color: #ff66b2;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 5px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #ff99cc;
}

button:focus {
  outline: none;
  box-shadow: 0 0 5px #ff66b2;
}

/* Estilo para resaltar los cuadrados ganadores */
.square.highlight {
  background-color: #f83f9c; /* Un tono de rosa más brillante */
}
